export default{
	data(){
		return{
			dataList:'',
			url:'&r=api.member.address',
			uid:'',
			token:'',
			mode:''
		}
	},
	mounted(){
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getAddressList()
		}
		
		uni.$on('updateAddressList',function(){
			that.getAddressList()
		})
	},
	onLoad(e){
		if(e.type=='choose'){
			this.mode=1
		}
	},
	methods:{
		toChange(t,it){
			uni.navigateTo({
				url:'./addressChange?type='+t+'&item='+JSON.stringify(it)
			})
		},
		getAddressList(){
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
				page:1,
				pagesize:99,
			}
			  this.$apiPost(this.url,params).then((res) =>{
				that.dataList=res.data	
			  })
		},
		longpress(id,item){
			var that=this,list
			if(this.mode==1){
				list=['删除','选为收货地址']
			}else{
				list=['删除']
			}
			uni.showActionSheet({
			    itemList: list,
			    success: function (res) {
					console.log(res)
					if(res.tapIndex==0){
						that.deleteAddress(id)
					}else if(res.tapIndex==1){
						uni.$emit('chooseAddress',item)
						uni.navigateBack({
							delta:1
						})
					}
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		},
		deleteAddress(id){
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
				addressid:id
			}
			var url='&r=api.member.address.delete'
			  this.$apiPost(url,params).then((res) =>{
				  that.$msg('删除成功')
				  setTimeout(function(){
					  that.getAddressList()
				  },0)
			  })
		}
	}
}
